* {box-sizing:border-box;}
body {
    background-color:#2f2f2f;
    margin:0; 
    padding:0;
    height:100%;
    font-family:sans-serif
}


html, body, #root, .container {height:100%;}

.loader {
    width:100; height:100%; display:flex; justify-content: center; align-items:center;
}

.movies {display:flex; justify-content:space-between; flex-wrap:wrap; padding:50px; width:80%;}
.movies .movie {width:45%; background:#fff; margin-bottom:70px; display:flex; align-items:flex-start; justify-content: space-between;}

.movie img {max-width:120px; margin-right:30px;}
.movie p {font-weight:300;}